  checkDisable(name: string): boolean {
    if (this.$route.name === name) {
      return false
    }
    return true
  }
  convertNumberToDecimal(value: string): void {
    this.dataInput[value] =
      //@ts-ignore
      this.$options.filters.formatNumber(this.convertDecimalToNumber(this.dataInput[value])) === '0'
        ? null
        : //@ts-ignore
          this.$options.filters.formatNumber(this.convertDecimalToNumber(this.dataInput[value]))
  }

  convertDecimalToNumber(number: string): number {
    return Number(number.replaceAll(',', ''))
  }

  handleChangeInput(type: string, value: string): void {
    this.convertNumberToDecimal(value)
    if (type === 'basicAmount') {
      if (
        this.dataInput.toBasicAmount &&
        this.dataInput.fromBasicAmount &&
        this.convertDecimalToNumber(this.dataInput.toBasicAmount) < this.convertDecimalToNumber(this.dataInput.fromBasicAmount)
      ) {
        this.listError.basicAmount = true
      } else {
        this.listError.basicAmount = false
      }
    }
    if (type === 'extraAmount') {
      if (
        this.dataInput.toExtraAmount &&
        this.dataInput.fromExtraAmount &&
        this.convertDecimalToNumber(this.dataInput.toExtraAmount) < this.convertDecimalToNumber(this.dataInput.fromExtraAmount)
      ) {
        this.listError.extraAmount = true
      } else {
        this.listError.extraAmount = false
      }
    }
    if (type === 'totalAmount') {
      if (
        this.dataInput.toTotalAmount &&
        this.dataInput.fromTotalAmount &&
        this.convertDecimalToNumber(this.dataInput.toTotalAmount) < this.convertDecimalToNumber(this.dataInput.fromTotalAmount)
      ) {
        this.listError.totalAmount = true
      } else {
        this.listError.totalAmount = false
      }
    }
  }

  checkDataRoute(value: string): any {
    //@ts-ignore
    // const value = this.$route.name
    if (value === 'new') {
      return 1
    }
    if (value === 'cancel') {
      return 2
    }
    if (value === 'effective') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'contract') {
      return ''
    }
    if (value === 'pending') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'refuse') {
      return 4
    }
  }
