  async getProduct(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.apiProduct.getProduct({
        search: trim(search),
        page: this.queryProduct.page,
        limit: this.queryProduct.limit
      })
      if (isAdd) {
        const a = result.content || []
        this.listProduct = [...this.listProduct, ...a]
      } else {
        this.listProduct = result.content || []
      }
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.listProduct = []
    }
  }
  get dateOption(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate(time)
      }
    }
  }
  get pickerOption(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate1(time)
      }
    }
  }
  get pickerOption1(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate2(time)
      }
    }
  }
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  get pickerOption2(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate3(time)
      }
    }
  }
  get pickerOption3(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate4(time)
      }
    }
  }

  disabledDate(time: Date): any {
    if (this.dataInput.fromApplyFormDate != null) {
      return time.getTime() < this.dataInput.fromApplyFormDate + 8.64e7
    }
  }
  disabledDate1(time: Date): any {
    if (this.dataInput.fromHandOverDate) return time.getTime() < this.dataInput.fromHandOverDate + 8.64e7
  }
  disabledDate2(time: Date): any {
    if (this.dataInput.fromIssueDate) return time.getTime() < this.dataInput.fromIssueDate + 8.64e7
  }
  disabledDate3(time: Date): any {
    if (this.dataInput.fromLastPayDate) return time.getTime() < this.dataInput.fromLastPayDate + 8.64e7
  }
  disabledDate4(time: Date): any {
    if (this.dataInput.fromExpireDate) return time.getTime() < this.dataInput.fromExpireDate + 8.64e7
  }
