  async getAreaList(query: string): Promise<any> {
    const temp = {
      search: trim(query),
      type: 1,
      districtId: this.user?.districts?.[0].id,
      i: 0,
      ps: 20
    }
    await this.apiUser
      .getAreaList(temp)
      .then((res: any) => {
        this.areaList = res.data
        if (this.getDisableArea) {
          // @ts-ignore
          // const isMember = this.role.userRole.length === 1 && this.role.userRole.find(role => role === 'TRANSACTION_OFFICE_MEMBER')
          this.dataInput.areaIdArray.push(this.user.areaId)

          this.cloneAreaIdArray = [...this.dataInput.areaIdArray]
        }
      })
      .catch(() => {
        this.isLoading = false
