  async getBranch(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })
      if (isAdd) {
        const a = result || []
        this.branchList = [...this.branchList, ...a]
      } else {
        this.branchList = result || []
      }
      if (this.getDisableArea) {
        // @ts-ignore
        // const isMember = this.role.userRole.length === 1 && this.role.userRole.find(role => role === 'TRANSACTION_OFFICE_MEMBER')
        if (filter(this.branchList, (item: any) => item.id === this.user.branchId).length) {
          this.dataInput.branchIdArray.push(this.user.branchId)
        } else {
          const item = {
            id: this.user.branchId,
            name: this.user.branchName,
            locationCode: this.user.branchCode
          }
          this.branchList.push(item)
          this.dataInput.branchIdArray.push(this.user.branchId)
        }

        this.cloneBranchIdArray = [...this.dataInput.branchIdArray]
      }
    } catch (error) {
      this.isLoading = false
      this.branchList = []
    }
  }
