  async getTransactionOffice(search: string, isAdd = false): Promise<any> {
    try {
      const result = await await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps
      })
      if (isAdd) {
        const a = result || []
        this.transactionList = [...this.transactionList, ...a]
      } else {
        this.transactionList = result || []
      }
      //fill tên dvkd tương ứng với quyền
      if (this.getDisableTransaction) {
        // @ts-ignore
        // const isMember = this.role.userRole.length === 1 && this.role.userRole.find(role => role === 'TRANSACTION_OFFICE_MEMBER')
        if (filter(this.transactionList, (item: any) => item.id === this.user.transactionOfficeId).length) {
          this.dataInput.transOfficeIdArray.push(this.user.transactionOfficeId)
        } else {
          const item = {
            id: this.user.transactionOfficeId,
            name: this.user.transactionOfficeName,
            locationCode: this.user.transactionOfficeCode
          }
          this.transactionList.push(item)
          this.dataInput.transOfficeIdArray.push(this.user.transactionOfficeId)
        }

        this.cloneOfficeIdArray = [...this.dataInput.transOfficeIdArray]
      }
    } catch (error) {
      this.isLoading = false
      this.transactionList = []
    }
  }

