        const listFilter = [...new Map(list.map((item: Record<string, any>) => [item[key], item])).values()]
        this.members = listFilter
        this.loadingAll = false
      })
      .catch(() => {
        this.loadingAll = false
        this.isLoading = false
      })
  }
