  checkResetData(): void {
    if (!this.getDisableArea) {
      this.dataInput.areaIdArray = []
      this.dataInput.branchIdArray = []
    }
    if (!this.getDisableSaleMember) {
      this.dataInput.salesMemberIdArray = []
    }
    if (!this.getDisableTransaction) {
      this.dataInput.transOfficeIdArray = []
    }
  }
  @Watch('$route.name')
  routeChange(new_: string): void {
    this.handleReset()
    this.checkTypeContract(new_)
    this.checkRouteStatus(new_)
  }

  checkRouteStatus(value: string): void {
    switch (value) {
      case 'dang-doi-soat':
        this.dataInput.auditStatus = '2'
        break
      case 'cho-duyet':
        this.dataInput.auditStatus = '3'
        break
      case 'tu-choi':
        this.dataInput.auditStatus = '6'
        break
      case 'da-duyet':
        this.dataInput.auditStatus = '4'
        break
      case 'yeu-cau-hieu-chinh':
        this.dataInput.auditStatus = '5'
        break
      case undefined:
        this.dataInput.auditStatus = '1'
        break
    }
  }

  checkTypeContract(type: string): void {
    switch (type) {
      case 'new':
        this.dataInput.contractStatus = 1
        break
      case 'cancel':
        this.dataInput.contractStatus = 2
        break
      case 'renew':
        this.dataInput.contractStatus = 3
        break

      default:
        this.dataInput.contractStatus = null
        break
    }
  }
  convertTimestampToDate(dateInput: Date): any {
    if (dateInput) {
      let timestamp = dateInput
      let date_not_formatted = new Date(timestamp)

      let formatted_string = date_not_formatted.getFullYear() + '-'

      if (date_not_formatted.getMonth() < 9) {
        formatted_string += '0'
      }
      formatted_string += date_not_formatted.getMonth() + 1
      formatted_string += '-'

      if (date_not_formatted.getDate() < 10) {
        formatted_string += '0'
      }
      formatted_string += date_not_formatted.getDate()
      console.log('format', formatted_string)
      return formatted_string
    } else {
      return null
    }

    // console.log(formatted_string)
  }
  handleSave(): void {
    let check = this.$route?.name
    this.setReportFilter({
      contract: this.type,
      filters: {
        ...this.dataInput
      }
    })

    if (check) {
      let list = {
        ...this.dataInput,
        fromApplyFormDate: this.convertTimestampToDate(this.dataInput.fromApplyFormDate),
        toApplyFormDate: this.convertTimestampToDate(this.dataInput.toApplyFormDate),
        fromIssueDate: this.convertTimestampToDate(this.dataInput.fromIssueDate),
        toIssueDate: this.convertTimestampToDate(this.dataInput.toIssueDate),
        fromLastPayDate: this.convertTimestampToDate(this.dataInput.fromLastPayDate),
        toLastPayDate: this.convertTimestampToDate(this.dataInput.toLastPayDate),
        fromExpireDate: this.convertTimestampToDate(this.dataInput.fromExpireDate),
        fromHandOverDate: this.convertTimestampToDate(this.dataInput.fromHandOverDate),
        toHandOverDate: this.convertTimestampToDate(this.dataInput.toHandOverDate),
        toExpireDate: this.convertTimestampToDate(this.dataInput.toExpireDate),
        fromBasicAmount: this.dataInput.fromBasicAmount ? this.convertDecimalToNumber(this.dataInput.fromBasicAmount) : null,
        toBasicAmount: this.dataInput.toBasicAmount ? this.convertDecimalToNumber(this.dataInput.toBasicAmount) : null,
        fromTotalAmount: this.dataInput.fromTotalAmount ? this.convertDecimalToNumber(this.dataInput.fromTotalAmount) : null,
        toTotalAmount: this.dataInput.toTotalAmount ? this.convertDecimalToNumber(this.dataInput.toTotalAmount) : null,
        fromExtraAmount: this.dataInput.fromExtraAmount ? this.convertDecimalToNumber(this.dataInput.fromExtraAmount) : null,
        toExtraAmount: this.dataInput.toExtraAmount ? this.convertDecimalToNumber(this.dataInput.toExtraAmount) : null,

        // areaIdArray: this.dataInput.areaIdArray?.length ? this.dataInput.areaIdArray.join(',') : null,
        // branchIdArray: this.dataInput.branchIdArray?.length ? this.dataInput.branchIdArray.join(',') : null,
        // consultantMemberIdArray: this.dataInput.consultantMemberIdArray?.length ? this.dataInput.consultantMemberIdArray.join(',') : null,
        // productIdArray: this.dataInput.productIdArray?.length ? this.dataInput.productIdArray.join(',') : null,
        // refMemberIdArray: this.dataInput.refMemberIdArray?.length ? this.dataInput.refMemberIdArray.join(',') : null,
        // salesMemberIdArray: this.dataInput.salesMemberIdArray?.length ? this.dataInput.salesMemberIdArray.join(',') : null,
        // transOfficeIdArray: this.dataInput.transOfficeIdArray?.length ? this.dataInput.transOfficeIdArray.join(',') : null,
        searchType: 'CONTRACT',
        contractStatus: this.checkDataRoute(check)
      }

      this.$emit('apply', list)
      this.setOpenPopup({
        popupName: 'audit-filter',
        isOpen: false
      })
    }
  }
